import React, { Component } from 'react';

class Registration extends Component  {
  constructor (props) {
    super();
    this.state = {
      email: '',
      password: '',
      name: ''
    }
  }

  onSubmitSignIn = () => {
    fetch('http://localhost:3000/register', {
      method: 'post',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({
        email: this.state.email,
        password: this.state.password,
        name: this.state.name
      })
    })
    .then(response => response.json(response))
    .then(user => {
      if (user.id) {
        this.props.loadUser(user)
        this.props.onRouteChange('home');
      }
    })
    .catch(err => {console.log(err)})
  }

  onNameChange = (event) => {
    if(event.target.value.length >= 5) 
    {
      this.setState({name: event.target.value});
    } else {
      console.log('invalid name - name must contain 5 to 20 characters')
    }
  }

  onEmailChange = (event) => {
    if(event.target.value.search('@') > 0 && event.target.value.length >= 5) 
      {
        this.setState({email: event.target.value});
      } else {
        console.log('invalid email');
      }
  }
  
  onPasswordChange = (event) => {
    const strongRegex = new RegExp("^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*])(?=.{8,50})");
    if(strongRegex.test(event.target.value))
    {
      this.setState({password: event.target.value})
    } else {
      console.log('invalid password - password must contain 8 to 50 characters with at least one lowercase letter, one uppercase letter, one numeric digit, and one special character');
    }
  }  

  render () {
    return (
      <article className="br3 ba dark-gray b--black-10 mv4 w-100 w-50-m w-25-l mw6 center shadow-5">      
        <main className="pa4 black-80">
          <div className="measure">
            <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
              <legend className="f4 fw6 ph0 mh0">Register</legend>
              <div className="mt3">
                <label className="db fw6 lh-copy f6" htmlFor="email-address">Name</label>
                <input 
                onChange={this.onNameChange}
                className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
                type="text" 
                name="name"  
                id="name" />
              </div>
              <div className="mt3">
                <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
                <input 
                onChange={this.onEmailChange}
                className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
                type="email" 
                name="email-address"  
                id="email-address" />
              </div>
              <div className="mv3">
                <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
                <input 
                onChange={this.onPasswordChange}
                className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
                type="password" 
                name="password"  
                id="password" />
              </div>
            </fieldset>
            <div className="">
              <input 
                onClick={this.onSubmitSignIn}
                className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" 
                type="submit" 
                value="Sign up" />
            </div>
          </div>
        </main>
      </article>
    ); 
  }
}

export default Registration;